.wp-block-create-block-modal-builder-block {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

.htr-modal-content-hide {
  opacity: 0;
  z-index: -999999999;
}

.htr-modal-content-show {
  opacity: 1;
  z-index: 99999;
}

.htr-modal-prevent-scroll {
  overflow: hidden;
}

.htr-modal-content {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 0;
  border: 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.htr-modal-content-wrap {
  max-height: 80%;
  max-width: 80%;
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
  position: relative;
  background-color: white;
}

.htr-modal-content-wrap::-webkit-scrollbar {
  /* WebKit */
  width: 0px;
}

.htr-modal-content-close-btn {
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 38px;
  height: 38px;
  z-index: 9999;
  color: white;
  cursor: pointer;
  line-height: 32px;
  font-size: 1.6rem;
  text-align: center;
  border-radius: 100%;
  background-color: black;
  border: 2px solid white;
}

.htr-modal-content-background {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

@media (min-width: 1280px) {
  .htr-modal-content-wrap {
    max-width: 1280px;
  }
}
